.Services{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-wrap: wrap;
}
 .svg {
 cursor: pointer;
 width: 200px;
 height: 300px;
 margin: 50px;
  text-align: center;
  border:rgb(88,88,88) 1px solid;
  border-radius: 20px;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
 .svg, h2, h5 {
     color:	rgb(88,88,88);
 }